.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 50px;
    margin-top: 100px;
    padding: 20px;
    box-sizing: border-box;
}

.footer-logo {
    display: flex;
    align-items: center;
    gap: 20px;
}

.footer-logo p {
    color: #383838;
    font-size: 2.875rem; /* 46px */
    font-weight: 700;
}

.footer-links {
    display: flex;
    list-style: none;
    gap: 50px;
    color: #252525;
    font-size: 1.25rem; /* 20px */
}

.footer-links li {
    cursor: pointer;
}

.footer-social-icon {
    display: flex;
    gap: 20px;
}

.footer-icons-container {
    padding: 10px;
    padding-bottom: 6px;
    background: #fbfbfb;
    border: 1px solid #ebebeb;
}

.footer-copyright {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: #1a1a1a;
    font-size: 1.25rem; /* 20px */
}

.footer-copyright hr {
    width: 80%;
    border: none;
    border-radius: 10px;
    height: 3px;
    background: #c7c7c7;
}

@media (max-width: 1024px) {
    .footer {
        gap: 30px;
        padding: 20px;
    }

    .footer-logo p {
        font-size: 2.25rem; /* 36px */
    }

    .footer-links {
        gap: 30px;
    }

    .footer-copyright {
        font-size: 1.125rem; /* 18px */
    }
}

@media (max-width: 768px) {
    .footer {
        gap: 20px;
        padding: 15px;
    }

    .footer-logo p {
        font-size: 1.875rem; /* 30px */
    }

    .footer-links {
        flex-direction: column;
        gap: 20px;
        font-size: 1rem; /* 16px */
    }

    .footer-copyright {
        font-size: 1rem; /* 16px */
    }
}

@media (max-width: 480px) {
    .footer {
        gap: 15px;
        padding: 10px;
    }

    .footer-logo p {
        font-size: 1.5rem; /* 24px */
    }

    .footer-links {
        font-size: 0.875rem; /* 14px */
    }

    .footer-copyright {
        font-size: 0.875rem; /* 14px */
    }

    .footer-copyright hr {
        width: 90%;
    }
}
